<template>
  <div>
    <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('search')}">
      <input
        v-validate="'required|regex:^([a-zA-Z0-9\\s-]+)?$|min:3'"
        name="search" id="search"
        class="mdl-textfield__input"
        v-model.trim="search"
        >
      <label class="mdl-textfield__label" for="search">{{placeholder}}</label>
      <span class="mdl-textfield__error">{{ errors.first('search') }}</span>
    </div>
    <button
      class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button  custom-button--primary"
      @click="emitSearchValue"
    >
      Search
    </button>
  </div>
</template>
<script>
export default {
  name: 'search-filter',
  props: {
    placeholder: {
      type: String,
      default: 'Search'
    }
  },
  data () {
    return {
      search: ''
    }
  },
  mounted () {
    window.componentHandler.upgradeAllRegistered()
  },
  methods: {
    emitSearchValue () {
      this.$validator.validateAll().then(valid => {
        if (valid) {
          this.$emit('search', this.search)
        }
      })
    }
  }
}
</script>



